.Horario-bloque {
    animation: blink 1s ease-out;
    z-index: 10;
    position: relative;
    width: 100%;
    transition: background-color .25s ease-out;
}

.Horario-header {
    font-size: 20px;
}

.Horario-bloque.middle {
    padding-top: 8px;
    padding-bottom: 15px;
    translate: 0 0;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
}

.Horario-bloque.top {
    padding-bottom: 10px;
    translate: 0 10px;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
}

.Horario-bloque.bottom {
    padding-top: 10px;
    translate: 0 -10px;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
}

.Horario-tope_warning {
    transition: all 2s ease;
    background-image: repeating-linear-gradient(45deg, #aaa4 0, #aaa4 5px, transparent 0, transparent 50%);
    background-size: 20px 20px;
    background-color: #000;
    animation: tope-warning-scroll 1.5s infinite linear;

    &.ghosted {
        background-blend-mode: luminosity;
    }

    &.ex_catedra {
        background-image: repeating-linear-gradient(45deg, #f91a 0, #f91a 5px, transparent 0, transparent 50%);
    }
}


.Horario-dialog-bloque {
    position: relative;
    padding: 15px 5px;
    min-width: 10%;
    justify-content: center;
    align-items: center;
    display: flex;

    &.ayudantia {
        opacity: 0.5;
    }

    &.tope {
        background-image: repeating-linear-gradient(45deg, #f915 0, #f915 5px, transparent 0, transparent 50%);
        background-size: 20px 20px;
        background-color: #222;
        animation: tope-warning-scroll 1.5s infinite linear;
    }
}

.Horario-dialog-bloque_number {
    position: absolute;
    top: 0;
    left: 0;
    margin: 4px;
    scale: .8;
    opacity: 0.5;
    font-weight: bolder;
}

.Horario-dialog-bloque_sala {
    position: absolute;
    bottom: 0;
    right: 0;
    margin: 2px;
    scale: .8;
    opacity: 0.5;
}

.Horario-dialog-bloque_day {
    padding: 0px 15px;
}

@keyframes tope-warning-scroll {
    100% {
        background-position: 20px 0;
    }
}

@keyframes blink {
    0% {
        border-width: 10px;
    }

    100% {
        border-width: 4px;
    }
}